import { BookCopy, Users, MessageSquareMore } from "lucide-react";
import Link from "next/link";

const accessLinks = [
    {
        icon: <BookCopy className="h-8 w-8 text-blue-600" />,
        title: "My Courses",
        href: "/dashboard/courses"
    },
    {
        icon: <Users className="h-8 w-8 text-blue-600" />,
        title: "Study Groups",
        href: "/groups"
    },
    {
        icon: <MessageSquareMore className="h-8 w-8 text-blue-600" />,
        title: "Community Forum",
        href: "/forum"
    }
];

const QuickAccess = () => {
    return (
        <section className="py-20 bg-gray-50">
            <div className="container mx-auto px-4 sm:px-6 lg:px-8">
                <h2 className="text-3xl font-bold text-center mb-12">Quick Access</h2>
                <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
                    {accessLinks.map((link) => (
                        <Link href={link.href} key={link.title}>
                            <div className="flex items-center p-6 bg-white rounded-lg shadow-md hover:shadow-lg transition-shadow cursor-pointer">
                                {link.icon}
                                <span className="ml-4 text-xl font-medium text-gray-800">{link.title}</span>
                            </div>
                        </Link>
                    ))}
                </div>
            </div>
        </section>
    );
};

export default QuickAccess; 